import { Injectable } from '@angular/core';
import { Observable, from, fromEvent } from 'rxjs';
import { switchMap, map } from 'rxjs/operators';

const nav = window.navigator;

export interface IPermissionsResult {
  video: boolean;
  audio: boolean;
}

/**
 * Unified error class thrown for permission errors (which otherwise vay vary
 * depending on browser version).
 */
export class WebrtcPermissionError extends Error {}

@Injectable({ providedIn: 'root' })
export class WebrtcHelper {
  private _hasDisplayMedia =
    nav.mediaDevices && 'getDisplayMedia' in nav.mediaDevices;

  get hasDisplayMedia() {
    return this._hasDisplayMedia;
  }

  /**
   * Throw WebrtcPermissionError if error is a permission error, re-throw
   * original error else.
   */
  private _throwWrappedPermissionError(err: Error): void {
    if (this.isPermissionError(err)) {
      throw new WebrtcPermissionError();
    }
    throw err;
  }

  /**
   * Identical to navigator.mediaDevices.getUserMedia(), but throws custom
   * WebrtcPermissionError for permission errors instead original errors (to
   * allow easy instanceof checking).
   */
  async getUserMedia(
    constraints: MediaStreamConstraints,
  ): Promise<MediaStream> {
    try {
      return await navigator.mediaDevices.getUserMedia(constraints);
    } catch (err) {
      this._throwWrappedPermissionError(err);
    }
  }

  /**
   * Identical to navigator.mediaDevices.getUserMedia() but throws custom
   * WebrtcPermissionError for permission errors instead original errors (to
   * allow easy instanceof checking).
   */
  async getDisplayMedia(
    constraints: MediaStreamConstraints,
  ): Promise<MediaStream> {
    if (this._hasDisplayMedia) {
      try {
        return await (navigator.mediaDevices as any).getDisplayMedia(
          constraints,
        );
      } catch (err) {
        this._throwWrappedPermissionError(err);
      }
    } else {
      throw new Error(
        'getDisplayMedia() not supported, ' +
          'make sure chrome://flags/#enable-experimental-web-platform-features is enabled',
      );
    }
  }

  /**
   * Get list of all 'input' media devices.
   */
  async getSources() {
    // Return empty list for unsupported browsers.
    if (!nav.mediaDevices) {
      return [];
    }
    const srcs = await nav.mediaDevices.enumerateDevices();
    return srcs
      .filter(function (src) {
        return src.kind.endsWith('input');
      })
      .map(function (dev) {
        return {
          id: dev.deviceId,
          label: dev.label,
          kind: dev.kind.substring(0, dev.kind.length - 5),
        };
      });
  }

  /**
   * Get all input sources of given kind.
   */
  async getSourcesByKind(kind: string) {
    const sources = await this.getSources();
    return sources.filter(function (source) {
      return source.kind === kind;
    });
  }

  /**
   * Get list of all video input devices.
   */
  getVideoSources() {
    return this.getSourcesByKind('video');
  }

  /**
   * Get list of all audio input devices.
   */
  getAudioSources() {
    return this.getSourcesByKind('audio');
  }

  /**
   * Check if user has a camera connected.
   */
  async hasCamera() {
    const cams = await this.getVideoSources();
    return cams.length > 0;
  }

  /**
   * Check if user has a mic connected.
   */
  async hasMicrophone() {
    const mics = await this.getAudioSources();
    return mics.length > 0;
  }

  hasLabel(sources: { label: string }[]): boolean {
    return sources.some(function (source) {
      return !!source.label;
    });
  }

  /**
   * Check if App has video access without triggering a request prompt.
   * We assume that source label is empty if permission has not been granted yet.
   * This is how Chrome handles permissions for secure-origin sites.
   */
  async hasVideoAccess() {
    return this.hasLabel(await this.getVideoSources());
  }

  /**
   * See hasVideoAccess
   */
  async hasAudioAccess() {
    return this.hasLabel(await this.getAudioSources());
  }

  /**
   * Get stream for all device types that are available and immediately
   * stop streams them again.
   * Intended for requesting access on secure-origin pages, where access setting
   * is remembered by the browser between consecutive requests.
   * Returns promise that resolves with object:
   * {audio: hasGrantedAudioAccess, video: hasGrantedVideoAccess}
   */
  async getCameraAndMicPermission(): Promise<IPermissionsResult> {
    const hasCam = await this.hasCamera();
    let audio = false;
    let video = false;
    try {
      const stream = await navigator.mediaDevices.getUserMedia({
        audio: true,
        video: hasCam,
      });
      video = !!stream.getVideoTracks().length;
      audio = !!stream.getAudioTracks().length;
      stream.getTracks().forEach(function (track) {
        track.stop();
      });
    } catch (_) {
      // ignored
    }
    return { video, audio };
  }

  /**
   * Checks permission state of given permission name
   */
  async checkPermission(name: PermissionName): Promise<PermissionState> {
    const status = await navigator.permissions.query({
      name,
    });
    return status.state;
  }

  /***
   * Listens for permission change of given permission name
   */
  onPermission(name: PermissionName): Observable<PermissionStatus> {
    return from(navigator.permissions.query({ name })).pipe(
      switchMap((status) => fromEvent(status, 'change')),
      map((event) => event.currentTarget as PermissionStatus),
    );
  }

  /**
   * Check if given error is due to webrtc permissions not having been granted yet.
   */
  isPermissionError(err: Error) {
    return (
      err &&
      (err.name === 'PermissionDeniedError' ||
        err.name === 'MediaDeviceFailedDueToShutdown' || // still needed?
        err.name === 'NotAllowedError' ||
        err.name === 'InvalidStateError')
    ); // Still needed?
  }
}
