import './polyfills';
import platform from './tools/platform';
import * as chromeMessaging from './tools/chrome-messaging';
import { WebrtcHelper } from './tools/webrtc';
import { asyncResponse } from '@castify/models';

const FRAME_READY_MSG = 'castifyDrawCamFrameReady';
// matches msg in cf-cam-frame, forwarded by bg-message
const CAM_MSG_WITH_RESULT = 'camFrameMsgWithResult';
// TODO: move to environment config.
const kDevServerOrigin = 'http://localhost:4200';

/**
 * Service living in iframe showing the webcam preview and providing a simple
 * messaging interface for retrieving the list of attached webcam devices (see
 * msgHandlers).
 */
function camFrameFactory(webrtc = new WebrtcHelper()) {
  function getStream(constraints: MediaTrackConstraints) {
    return webrtc.getUserMedia({ video: constraints });
  }

  let videoEl: HTMLVideoElement;
  let stream: MediaStream;
  const msgHandlers: any = {};

  function ensureDomCreated() {
    if (videoEl) {
      return;
    }
    videoEl = document.createElement('video');
    videoEl.volume = 0;
    document.body.appendChild(videoEl);
  }

  function disposeStream() {
    videoEl.pause();
    stream.getTracks().forEach(function (track) {
      track.stop();
    });
    stream = null;
  }

  /**
   * Start playback of video.
   * @param data webrtc video constraints
   * @returns that resolves with video resolution.
   */
  msgHandlers.start = function (data: MediaTrackConstraints): Promise<any> {
    if (stream) {
      disposeStream();
    }
    return getStream(data).then(function (s: MediaStream) {
      stream = s;
      const readyPromise = new Promise(function (resolve) {
        ensureDomCreated();
        videoEl.addEventListener('canplay', function onCanPlay() {
          videoEl.removeEventListener('canplay', onCanPlay);
          resolve({
            width: videoEl.videoWidth,
            height: videoEl.videoHeight,
          });
        });
      });
      videoEl.srcObject = stream;
      videoEl.play();
      return readyPromise;
    });
  };

  /**
   * Stop video playback and stream.
   */
  msgHandlers.stop = function () {
    disposeStream();
  };

  msgHandlers.getVideoDevices = function () {
    return webrtc.getVideoSources();
  };

  const devMsgRespond = asyncResponse.createRespond(function (msg: any) {
    window.parent.postMessage(msg, kDevServerOrigin);
  });

  function onChromeMessage(msg: any, sender: any, sendResponse: any) {
    if (msg && msg.type === CAM_MSG_WITH_RESULT) {
      // un-wrap nested msg addressed to us.
      msg = msg.data;
      // eslint-disable-next-line no-prototype-builtins
      if (msg && msgHandlers.hasOwnProperty(msg.type)) {
        chromeMessaging.respond(sendResponse, msgHandlers[msg.type](msg.data));
        return true;
      }
    }
    return false;
  }

  function errorsToString(err: any) {
    // errors cannot be cloned
    if (err instanceof Error) {
      return Promise.reject(err.toString());
    }
    return Promise.reject(err);
  }

  function onWindowMessage(ev: MessageEvent) {
    const msg = ev.data;
    // eslint-disable-next-line no-prototype-builtins
    if (msgHandlers.hasOwnProperty(msg.type)) {
      const promise = Promise.resolve(msgHandlers[msg.type](msg.data)).catch(
        errorsToString,
      );
      devMsgRespond(promise, msg);
    }
  }

  function installListeners() {
    if (platform.isExtension) {
      chrome.runtime.onMessage.addListener(onChromeMessage);
    } else {
      // eslint-disable-next-line no-console
      console.log('camFrame: using dev-only messaging');
      window.addEventListener('message', onWindowMessage);
    }
  }

  function postReady() {
    window.parent.postMessage(FRAME_READY_MSG, '*');
  }

  function init() {
    installListeners();
    postReady();
  }

  init();
}

camFrameFactory();
