import firebase from 'firebase/app';
import firestore = firebase.firestore;
import type { StringObjMap } from '../../collection-utils';
import type { sty } from '../../semantic-types';
import { FisCollection } from './fis-collection';
import { FisData } from './fis-data';
import {
  Property,
  Reference,
  setServerTimestamp,
  StringDataMap,
  Subcollection,
  Timestamp,
} from './fis-data-decorators';
import { FisRootModel } from './fis-root-model';
import { Observable } from 'rxjs';

/**
 * Example collection that demonstrates all capabilities of FisCollection
 * intended for testing
 */
export class FisExampleCollection extends FisCollection<FisExampleModel> {
  getAllBornAfter = this._buildGetAll(this._bornAfter);
  onAllBornAfter: (...args: any[]) => Observable<any> = this._buildOnAll(
    this._bornAfter,
  );
  onBornAfterAdd: (...args: any[]) => Observable<any> = this._buildOnAdd(
    this._bornAfter,
  );
  protected _bornAfter(birthDate: number): firestore.Query {
    return this.ref.where('birthDate', '>', birthDate).orderBy('birthDate');
  }
}

export class ExampleAddress extends FisData {
  @Property()
  zipcode: number;
  @Property()
  city: string;
}

export class ExampleSchool {
  name: string;
  constructor(fields: Object) {
    Object.assign(this, fields);
  }
  // NOTE: toJSON method will be invoked during serialization when class specifies that
}

export interface ExampleVoice {
  pitch: string;
}

/**
 * Example model that demonstrates all capabilities of FisModel
 * intended for testing
 */
export class FisExampleModel extends FisRootModel<FisExampleModel> {
  static readonly Collection = FisExampleCollection;
  static get root() {
    return this._getRoot<FisExampleCollection>('/example');
  }

  @Property()
  firstName: string;
  @Property()
  surname: string;
  @Property()
  birthDate: sty.Timestamp;
  @Property()
  hobbies: string[];
  @Property()
  voice: ExampleVoice;
  @Reference(() => FisExampleModel)
  partner?: FisExampleModel;
  @Subcollection(() => FisExampleCollection)
  children: FisExampleCollection;

  // timestamps
  @Timestamp(true)
  lastVisit: sty.Timestamp;
  @Timestamp()
  createdAt = setServerTimestamp;

  @Property(() => new ExampleAddress())
  address: ExampleAddress;
  @Property((obj) => new ExampleSchool(obj))
  school: ExampleSchool;
  @StringDataMap(() => ExampleAddress)
  addressbook: StringObjMap<ExampleAddress>;

  // Only deserialize, but do not serialize on save() (to avoid overriding
  // potentially changed values).
  // We use model.ref.update() directly for updating counters, until we've
  // proper support in models (see tests)
  @Property(undefined, true, false)
  count = 0;

  get fullName(): string {
    return `${this.firstName} ${this.surname}`;
  }

  get age(): number {
    return Date.now() - this.birthDate;
  }
}

// sample data that can be used for testing
export const tAge = 1000;
/**
 * representation of the model in the database
 */
export const tData = {
  firstName: 'John',
  surname: 'Doe',
  birthDate: 1000000 - tAge,
  address: {
    zipcode: 1234,
    city: 'Chicago',
  },
  school: {
    name: 'someSchool',
  },
  hobbies: ['sports', 'reading'],
  voice: { pitch: 'high' },
  addressbook: {
    fred: { zipcode: 123, city: 'someCity' },
    max: { zipcode: 321, city: 'Musterstadt' },
  },
};
/**
 * deserialized model data
 */
export const tModelData = {
  ...tData,
  address: new ExampleAddress(tData.address),
  school: new ExampleSchool(tData.school),
  addressbook: {
    fred: new ExampleAddress(tData.addressbook.fred),
    max: new ExampleAddress(tData.addressbook.max),
  },
} as Partial<FisExampleModel>;
