/**
 * Wrapper for  chrome.runtime that messages like this:
 * {type: type, data: data}
 * resolves with resultValue for responses like:
 * {type: 'result', data: resultValue}
 * or rejects with someError for responses like
 * {type: 'error', data: someError}
 */
export function sendMessage(type: string, data: any) {
  return new Promise(function (resolve, reject) {
    chrome.runtime.sendMessage({ type, data }, function (response) {
      if (chrome.runtime.lastError) {
        reject(chrome.runtime.lastError);
      } else if (!response) {
        reject();
      } else if (response.type === 'result') {
        resolve(response.data);
      } else if (response.type === 'error') {
        reject(response.data);
      }
    });
  });
}

function errorsToString(err: Error | string) {
  // errors cannot be cloned
  if (err instanceof Error) {
    return Promise.reject(err.toString());
  }
  return Promise.reject(err);
}

/**
 * Send response via chrome messaging
 * Response value or Promise that resolves to response value.
 */
export function respond(
  sendResponse: (d: any) => void,
  response: Promise<any> | any,
) {
  Promise.resolve(response)
    .catch(errorsToString)
    .then(
      function (result) {
        sendResponse({ type: 'result', data: result });
      },
      function (error) {
        sendResponse({ type: 'error', data: error });
      },
    );
}
