import firebase from 'firebase/app';
import { fisApiCompat, usingNodeApi } from '../firestore';
import { FisCollection } from './fis-collection';
import { FisData } from './fis-data';
import { FisModel, FisModelConstructor } from './fis-model';
import firestore = firebase.firestore;

/**
 * Declare that property should be exposed to the database
 * @param instanceFactory create a new instance of the data
 */
export function Property(
  instanceFactory?: (data: any) => FisData | Object,
  deserialize = true,
  serialize = true,
): PropertyDecorator {
  return function (target: any, propertyKey: string) {
    FisData._ensureDatastructures(target);

    // add serialization callback for property
    if (serialize) {
      target.__fisSerializations[propertyKey] = function doSerialize(
        instance: any,
      ) {
        if (!(propertyKey in instance) || instance[propertyKey] === undefined) {
          return undefined;
        } else if (instanceFactory) {
          if (
            !!instance[propertyKey] &&
            '_fisSerialize' in instance[propertyKey] &&
            instance[propertyKey]._fisSerialize instanceof Function
          ) {
            // handle FisData instances
            return instance[propertyKey]._fisSerialize();
          } else if (instance[propertyKey] instanceof Object) {
            return JSON.parse(
              JSON.stringify({
                // wrap in object to pass object key to toJSON method
                [propertyKey]: instance[propertyKey],
              }),
            )[propertyKey];
          }
        }
        return instance[propertyKey];
      };
    }

    // add deserialization callback for property
    if (deserialize) {
      target.__fisDeserializations[propertyKey] = function doDeserialize(
        obj: any,
      ) {
        if (instanceFactory && obj[propertyKey] instanceof Object) {
          const instance = instanceFactory(obj[propertyKey]);
          if (
            '_fisDeserialize' in instance &&
            instance._fisDeserialize instanceof Function
          ) {
            // handle FisData instances
            return instance._fisDeserialize(obj[propertyKey]);
          } else {
            // handle Serializable instances
            return instance;
          }
        }
        return obj[propertyKey];
      };
    }
  };
}

/**
 * Declare a dynamic map of FisData subclasses
 * @param dataType    type factory for FisData subtype
 */
export function StringDataMap(
  dataType: (data: any) => new (fields?: any) => FisData,
): PropertyDecorator {
  return function (target: any, propertyKey: string) {
    FisData._ensureDatastructures(target);

    // add serialization callback for property
    target.__fisSerializations[propertyKey] = function doSerialize(
      instance: any,
    ) {
      if (!instance[propertyKey]) {
        return {};
      } else if (instance[propertyKey] instanceof Object) {
        const obj = {};
        for (const [key, value] of Object.entries(instance[propertyKey])) {
          if (!(value instanceof FisData)) {
            throw new Error(
              '@StringDataMap expect entries in map to be instances of FisData',
            );
          }
          obj[key] = (value as FisData)._fisSerialize();
        }
        return obj;
      } else {
        throw new Error(
          '@StringDataMap decorator can only serialize properties of type Object',
        );
      }
    };

    // add deserialization callback for property
    target.__fisDeserializations[propertyKey] = function doDeserialize(
      obj: any,
    ) {
      if (!obj[propertyKey]) {
        return undefined;
      } else {
        const instance = {};
        for (const [key, value] of Object.entries(obj[propertyKey])) {
          instance[key] = new (dataType(value))()._fisDeserialize(value);
        }
        return instance;
      }
    };
  };
}

/**
 * Declare that a reference to the model stored in the property should be exposed to the database
 * @param modelType  type factory for model type
 */
export function Reference(
  modelType: (data: Object) => FisModelConstructor<FisModel<any>>,
): PropertyDecorator {
  return function (target: any, propertyKey: string) {
    FisData._ensureDatastructures(target);

    // add serialization callback for property
    target.__fisSerializations[propertyKey] = function doSerialize(
      instance: any,
      json: boolean,
    ) {
      if (json) {
        // We've no meaningful way to convert db references to pure json right now, so just skip.
        // We could change this, once we have a separate, serializeable Reference class.
        return undefined;
      } else if (!instance[propertyKey]) {
        return undefined;
      } else if (instance[propertyKey] instanceof FisModel) {
        return instance[propertyKey].ref; // only save reference
      } else {
        throw new Error(
          '@Reference decorator can only serialize properties of type FisModel',
        );
      }
    };

    // add deserialization callback for property
    target.__fisDeserializations[propertyKey] = function doDeserialize(
      obj: any,
    ) {
      if (!obj[propertyKey]) {
        return undefined;
      } else {
        return new (modelType(obj))(obj[propertyKey]);
      }
    };
  };
}

/**
 * like @Reference but works on arrays of models
 * @param modelType  type factory for model type
 */
export function ReferenceArray(
  modelType: (data: Object) => FisModelConstructor<FisModel<any>>,
): PropertyDecorator {
  return function (target: any, propertyKey: string) {
    FisData._ensureDatastructures(target);

    // add serialization callback for property
    target.__fisSerializations[propertyKey] = function doSerialize(
      instance: any,
    ) {
      if (!instance[propertyKey]) {
        return undefined;
      } else if (instance[propertyKey] instanceof Array) {
        return instance[propertyKey].map((model) => {
          if (!(model instanceof FisModel)) {
            throw new Error(
              '@ReferenceArray decorator can only serialize Arrays of FisModel instances',
            );
          }
          return model.ref;
        }); // only save references
      } else {
        throw new Error(
          '@ReferenceArray decorator can only serialize properties of type Array',
        );
      }
    };

    // add deserialization callback for property
    target.__fisDeserializations[propertyKey] = function doDeserialize(
      obj: any,
    ) {
      if (!obj[propertyKey] || !(obj[propertyKey] instanceof Array)) {
        return undefined;
      } else {
        return obj[propertyKey].map((data) => new (modelType(obj))(data));
      }
    };
  };
}

/**
 * Declare that a property stores a subcollection (note that nothing will actually be exposed to the database
 * because we can just define a subcollection by it's subpath)
 * @param collectionType  type factory for collection type
 */
export function Subcollection(
  collectionType: () => new (
    modelCls: any,
    ref: firestore.CollectionReference,
  ) => FisCollection<any>,
): PropertyDecorator {
  return function (target: any, propertyKey: string) {
    FisData._ensureDatastructures(target);
    target._subcollections.push(propertyKey);

    Object.defineProperty(target, propertyKey, {
      get: function getCollection() {
        if (this instanceof FisModel) {
          return new (collectionType())(
            this.constructor,
            this.ref.collection(propertyKey.toString()),
          );
        } else {
          throw new Error(
            '@Subcollection decorator can only be used in subclasses of FisModel',
          );
        }
      },
      enumerable: true,
      configurable: false,
    });
  };
}

/**
 * To be used with @Timestamp decorator.q
 * Indicates that the timestamp should be updated to server time on next write.
 */
export const setServerTimestamp = -1;

/**
 * Declare property that saves a timestamp. Firestore has it's own implementation for timestamps
 * this decorator translates between firestore timestamps and milliseconds.
 * setting a @Timestamp property to setServerTime will apply the server time on save.
 *
 * @param alwaysUpdate  always update to server timestamp on save (be careful with this: firestore has
 *                      a limit of 500 writes/s for indexed fields)
 */
export function Timestamp(alwaysUpdate: boolean = false): PropertyDecorator {
  return function (target: any, propertyKey: string) {
    FisData._ensureDatastructures(target);

    // add serialization callback for property
    target.__fisSerializations[propertyKey] = function doSerialize(
      instance: any,
      json: boolean,
    ) {
      const value = instance[propertyKey];
      // Return number for createdAt/updatedAt timestamps if set, undefined if not set yet.
      if (json) {
        if (setServerTimestamp === value) {
          return undefined;
        } else {
          return value;
        }
        // only update to server timestamp when either always update is set or property is marked for update
      } else if (alwaysUpdate || value === setServerTimestamp) {
        if (process.env.NODE_ENV === 'test' && !usingNodeApi()) {
          // use local time for local testing because we don't run through the server
          return fisApiCompat._timestamp().fromMillis(Date.now()); // use Date.now() to be able to mock with jasmine.clock()
        } else {
          return fisApiCompat._serverTimestamp();
        }
      } else if (value !== null && value >= 0) {
        return fisApiCompat._timestamp().fromMillis(value);
      } else {
        return null;
      }
    };

    // add deserialization callback for property
    target.__fisDeserializations[propertyKey] = function doDeserialize(
      obj: any,
    ) {
      if (obj[propertyKey] === null) {
        return null;
      } else if (!obj[propertyKey]) {
        return undefined;
      } else {
        return (obj[propertyKey] as firestore.Timestamp).toDate().valueOf();
      }
    };
  };
}

/**
 * Exposed publically for direct use with `model.update({key: increment(1)})` until we've
 * better integrated ways for dealing with counters in models.
 * Not a decorator, but special field value (like serverTime).
 */
export const increment = fisApiCompat._increment;

/**
 * Exposed publically for direct use with `db.runTransaction` until we've
 * better integrated ways for dealing with tranactions in models.
 * Not a decorator, but special field value (like serverTime).
 */
export const serverTimestamp = fisApiCompat._serverTimestamp;
