let factory;
let wrapper;

export function configure(fbFactory: () => [any, any]) {
  if (wrapper) {
    throw new Error(
      'fbWrapper.configure cannot be called after wrapper was used',
    );
  }
  factory = fbFactory;
}

function createWrapper(firebase, app) {
  return {
    api() {
      return firebase;
    },
    app() {
      return app;
    },
    database() {
      return app.database();
    },
    auth() {
      return firebase.auth();
    },
    messaging() {
      return app.messaging();
    },
    storage(bucketName?: string) {
      return app.storage(bucketName);
    },
  };
}

function getWrapper() {
  if (!wrapper) {
    if (!factory) {
      throw new Error(
        'fbWrapper.configure() not called before using wrapper. ' +
          'Make sure you only import the main edit-models module (no direct file imports)',
      );
    }
    const [firebase, app] = factory();
    wrapper = createWrapper(firebase, app);
  }
  return wrapper;
}

export default getWrapper;
