import firebase from 'firebase/app';
import firestore = firebase.firestore;
import {
  concat as observableConcat,
  from as observableFrom,
  Observable,
  Observer,
} from 'rxjs';
import {
  concatMap,
  distinctUntilChanged,
  filter,
  map,
  switchMap,
} from 'rxjs/operators';
import fbWrapper from '../fb/fb-wrapper';
import type { sty } from '../semantic-types';

let db;
export function getFirestore() {
  if (!db) {
    db = fbWrapper().app().firestore();
  }
  return db;
}

export function _injectDbRootForTesting(testDb) {
  db = testDb;
}

/**
 * determine whether we are using node or web api or web api
 * @return true if using node api, false it using web api
 */
export function usingNodeApi(db = getFirestore()): boolean {
  // account for mocked db root during tests
  const dbRoot = 'firestore' in db ? db.firestore : db;
  return !('disableNetwork' in dbRoot);
}

/**
 * helpers that hide away differences in api behaviour of firestore node and web api
 */
// eslint-disable-next-line @typescript-eslint/no-namespace
export namespace fisApiCompat {
  /**
   * run a query and output all immediately returned snapshots as an observable
   */
  export function _runQuery(
    query: firestore.Query,
  ): Observable<firestore.QueryDocumentSnapshot> {
    // TODO: we can add pagination here (https://firebase.google.com/docs/firestore/query-data/query-cursors)
    return observableFrom(query.get()).pipe(
      switchMap((querySnapshot) => observableFrom(querySnapshot.docs)),
    );
  }

  /**
   * Observe query using onSnapshot
   */
  export function _observeQuery(
    query: firestore.Query,
  ): Observable<firestore.QuerySnapshot> {
    return Observable.create((observer: Observer<firestore.QuerySnapshot>) => {
      const unsubscribeFn = query.onSnapshot(
        (querySnapshot) => observer.next(querySnapshot),
        (err) => observer.error(err),
        () => observer.complete(),
      );
      return unsubscribeFn;
    });
  }

  /**
   * observe added events from a query
   */
  export function _observeQueryAdd(
    query: firestore.Query,
  ): Observable<firestore.QueryDocumentSnapshot> {
    return _observeQuery(query).pipe(
      concatMap((snapshot) => snapshot.docChanges()),
      filter((change: firestore.DocumentChange) => change.type === 'added'),
      map((change: firestore.DocumentChange) => change.doc),
    );
  }

  /**
   * proxy onSnapshot method of a reference (query is also a valid reference) and return as observable
   */
  export function _observeRef(
    ref: firestore.DocumentReference,
  ): Observable<firestore.DocumentSnapshot> {
    if (usingNodeApi()) {
      return observableConcat(
        observableFrom(ref.get()), // hack: initial snapshot not allways captured by onSnapshot. Ensure deterministic behaviour
        // original implementation if onSnapshout would work as documented
        Observable.create((observer) => {
          const unsubscribeFn = ref.onSnapshot(
            (snapshot) => observer.next(snapshot),
            (err) => observer.error(err),
          );
          return unsubscribeFn;
        }),
      ).pipe(
        distinctUntilChanged(
          (x: firestore.DocumentSnapshot, y: firestore.DocumentSnapshot) =>
            _safeGetTimestamp(x, 'updateTime') ===
            _safeGetTimestamp(y, 'updateTime'),
        ),
      );
    } else {
      return Observable.create((observer) => {
        const unsubscribeFn = ref.onSnapshot(observer);
        return unsubscribeFn;
      });
    }
  }

  /**
   * return field value computed from Firebase.Timestamp, return undefined when not defined or accessible
   */
  export function _safeGetTimestamp(
    obj: Object,
    key: string,
  ): sty.Timestamp | undefined {
    if (obj) {
      const timestamp = obj[key];
      if (timestamp) {
        return timestamp.toDate().valueOf();
      }
    }
    return undefined;
  }

  /**
   * enhance snapshot with property readTime when not available
   */
  export function _ensureReadTime(
    snapshot: firestore.DocumentSnapshot,
  ): firestore.DocumentSnapshot {
    // readTime not available in firestore web api: emulate
    if (!('readTime' in snapshot)) {
      const readTime = new Date(Date.now());
      (snapshot as any).readTime = { toDate: () => readTime };
    }
    return snapshot;
  }

  /**
   * enhance writeResult with property writeTime when not available
   */
  export function _ensureWriteTime(writeResult: any): Object {
    // writeResult and writeTime not available in firestore web api: emulate
    if (!writeResult) {
      writeResult = {};
    }
    if (!('writeTime' in writeResult)) {
      const writeTime = new Date(Date.now());
      writeResult.writeTime = { toDate: () => writeTime };
    }
    return writeResult;
  }

  /**
   * check if obj is a DocumentSnapshot (api independent)
   */
  export function _isDocumentSnapshot(obj: any): boolean {
    return (
      obj.constructor.name ===
        firestore.DocumentSnapshot.prototype.constructor.name ||
      obj.constructor.name ===
        firestore.QueryDocumentSnapshot.prototype.constructor.name ||
      obj.constructor.name === 'TestOnlyNullDocumentSnapshot'
    ); // to inject fake snapshots during testing
  }

  /**
   * check if obj is a DocumentReference (api independent)
   */
  export function _isDocumentReference(obj: any): boolean {
    return (
      obj.constructor.name ===
      firestore.DocumentReference.prototype.constructor.name
    );
  }

  /**
   * get server timestamp implementation from current api
   */
  export function _serverTimestamp() {
    const serverTimestamp = fbWrapper()
      .api()
      .firestore.FieldValue.serverTimestamp();
    if (!serverTimestamp) {
      throw new Error(
        'could not obtain valid Timestamp implementation from firestore api',
      );
    }
    return serverTimestamp;
  }

  export function _timestamp() {
    return fbWrapper().api().firestore.Timestamp;
  }

  export function _increment(n: number) {
    return fbWrapper().api().firestore.FieldValue.increment(n);
  }
}
