import {
  defer as observableDefer,
  ReplaySubject,
  throwError as observableThrowError,
} from 'rxjs';

import { Observable, Subject } from 'rxjs';
import { catchError, switchMapTo } from 'rxjs/operators';
import { isObservable } from './is-observable';

export interface IMessage {
  type: string;
  data?: any;
  msgId?: number;
  isError?: boolean;
  isNext?: boolean;
  isComplete?: boolean;
  [x: string]: any;
}

export type ISendFn = (IMessage) => any | Promise<void>;

export type IRespondFn = (
  result: Promise<any> | Observable<any> | any,
  msg: IMessage,
) => void;

/**
 * Helper to  send messages that expects an async result message.
 * Message is sent as an object {type: type, data: data, msgId: msgId} with a unique msgId
 * property. Expects a result message of the following form:
 * {type: 'result', msgId: msgId, data: result, isComplete: true}
 * Or an error result message:
 * {type: 'result', isError: true, data: error}
 * Or an progress message:
 * {type: 'result', isNext: true, data: progress}
 *
 * The sender object provides an easy to use wrapper around this protocol.
 * Use:
 * - sendWithResponse(), if you expect a Promise-like response (error / result only)
 * - send(), if you do not expect any response. Omitts msgId from the message, instructing the
 * receiver to discard any result.
 * - sendWithProgress(), if you expect Observable-liekr responses (error / result / next message)
 *
 */
export class AsyncSender {
  private _msgCnt = 0;
  private _msgResolvers = new Map<number, Subject<any>>();

  constructor(private _sendFn: ISendFn) {}

  /**
   * Send message. Use sender.handleResult to handle result messages.
   * @return Promise that resolves with result;
   */
  sendWithResponse(type: string, data?: any): Promise<any> {
    return this.sendWithProgress(type, data).toPromise();
  }

  /**
   * Send message. Expect result message on sender.handleResult.
   * that resolves/rejects returned promise.
   * @return Observable with progress / completion events. Subscribing has no side effect.
   */
  sendWithProgress(type: string, data?: any): Observable<any> {
    // wrap response in promise
    const msgId = ++this._msgCnt;
    const subject = new ReplaySubject(1);
    this._msgResolvers.set(msgId, subject);
    return observableDefer(
      async () => await this._sendFn({ type, data, msgId }),
    ).pipe(
      catchError((err) => {
        // convert synchronous errors in send() to observables.
        delete this._msgResolvers[msgId];
        return observableThrowError(err);
      }),
      switchMapTo(subject.asObservable()),
    );
  }

  /**
   * Send message without expecting a result.
   */
  send(type: string | IMessage, data?) {
    if (typeof type === 'object') {
      this._sendFn(type);
    } else {
      this._sendFn({ type, data });
    }
  }

  /**
   * Handle result messages
   * @param msg message object
   * @return true if message was handled.
   */
  handleResult(msg?: IMessage) {
    if (msg && msg.type === 'result' && msg.msgId) {
      const subject = this._msgResolvers.get(msg.msgId);
      if (!subject) {
        throw new Error('asyncResponse: no resolver');
      }
      if (msg.hasOwnProperty('error') || msg.isError) {
        subject.error(msg.data);
        this._msgResolvers.delete(msg.msgId);
      } else {
        if (msg.isNext) {
          subject.next(msg.data);
        }
        if (msg.isComplete) {
          subject.complete();
          this._msgResolvers.delete(msg.msgId);
        }
      }
      return true;
    }
    return false;
  }
}

/**
 * @param sendFn send function.
 * @return function to respond to message. Call it
 * with the result you want to sent (Promise, Observable or value) and the original message
 * object to send a response for.
 */
export function createRespond(sendFn: ISendFn): IRespondFn {
  return function (
    result: Promise<any> | Observable<any> | any,
    msg: IMessage,
  ) {
    let nextWithComplete = true;
    const sendComplete = (data) => {
      sendFn({
        type: 'result',
        msgId: msg.msgId,
        data,
        isComplete: true,
        isNext: nextWithComplete,
      });
    };
    const sendError = (err) => {
      sendFn({ type: 'result', msgId: msg.msgId, data: err, isError: true });
    };

    Promise.resolve(result).then((resolvedResult) => {
      if (isObservable(resolvedResult)) {
        nextWithComplete = false;
        resolvedResult.subscribe(
          function onNext(value) {
            sendFn({
              type: 'result',
              msgId: msg.msgId,
              data: value,
              isNext: true,
            });
          },
          sendError,
          () => sendComplete(undefined),
        );
      } else {
        sendComplete(resolvedResult);
      }
    }, sendError);
  };
}
