import { fisApiCompat } from '../firestore';

/**
 * class that holds data that can be written to firestore or deserialize from firestore data
 * use decorators from fis-data-decorators.ts to define which properties should be exposed
 */
export abstract class FisData {
  static _ensureDatastructures(target: any): void {
    // NOTE: consider class inheritance when initializing data objects:
    // - hasOwnProperty checks if the current prototype (exluding parents) has a property
    // - when setting the initial value, we copy existing data from the parent prototype.
    //    classes are initialized in parent->child order, so properties from the parent have already been added.
    if (!target.hasOwnProperty('__fisSerializations'))
      target.__fisSerializations = { ...(target.__fisSerializations || {}) };
    if (!target.hasOwnProperty('__fisDeserializations'))
      target.__fisDeserializations = {
        ...(target.__fisDeserializations || {}),
      };
    if (!target.hasOwnProperty('_subcollections'))
      target._subcollections = [...(target._subcollections || [])];
  }

  /**
   * expose timestamp class depending on current api
   */
  static get Timestamp(): any {
    return fisApiCompat._timestamp();
  }

  constructor(fields?: Object) {
    if (fields) {
      Object.assign(this, fields);
    }
  }

  /**
   * Serialize object for storing it in firstore (json=false), or to pure json
   * for sending it over a http endpoint (json=true)
   */
  _fisSerialize(json = false): any {
    const obj = {};
    for (const [propertyKey, serializeFn] of Object.entries(
      (this as any).__fisSerializations || {},
    )) {
      const value = (serializeFn as Function)(this, json);
      if (value !== undefined) {
        obj[propertyKey] = value;
      }
    }
    return obj;
  }

  _fisDeserialize(obj: any): this {
    for (const [propertyKey, deserializeFn] of Object.entries(
      (this as any).__fisDeserializations || {},
    )) {
      // Only deserialize values that are not undefined, we don't want to
      // override class-property default values.
      // Individual property deserializers cannot override this behavior (i.e
      // they cannot provide their a 'default' value different from the one
      // used in class construciton).
      if (obj[propertyKey] !== undefined) {
        this[propertyKey] = (deserializeFn as Function)(obj);
      }
    }
    return this;
  }

  /**
   * Standard serialization signature. Typically used to return model via a http endpoint, or
   * to print serialization when used like this:
   *
   * @example
   * logging.info('foo %s', model);
   */
  toJSON(): Object {
    return this._fisSerialize(true);
  }
}
